import axios from 'axios'

const state = {
    products: [],
}

const getters = {
    
}

const actions = { //method
    getProducts({ commit }){
        axios.get('http://127.0.0.1:3000/products')
        .then(response => {
            commit('SET_PRODUCTS', response.data)
        })
    },
}

const mutations = {
    SET_PRODUCTS (state, products) {
        state.products = products
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}


