<template>
    <div class="container-wrapper" v-loading="loading">
        <div class="tio-section tio-section-top-seller">
            <div class="tio-section__header">
                <span class="tio-section__title">Gợi ý dành cho bạn</span>
            </div>
             <div class="tio-section__body">
                <div class="tio-section__body">
                    <div class="section-carousel-product">
                        <el-row>
                            <el-col :xs="12" :md="4" :lg="4" v-if="productIndex <= products.length" v-for="productIndex in productsToShow" :key="products[productIndex -1].id">
                                <router-link :to="{ name: 'ProductDetail', params: { Pid: products[productIndex-1].id }}">
                                    <ProductItem 
                                        :name="products[productIndex - 1].name"
                                        :image="products[productIndex - 1].images"
                                        :price="products[productIndex - 1].price"
                                        :list_price="products[productIndex - 1].list_price" 
                                        :quantity_sold="products[productIndex - 1].quantity_sold"
                                        :percent_discount="percent_discount(productIndex - 1)"
                                        :rating_average="products[productIndex - 1].rating_average"
                                        :havePriceDiscount="false" 
                                        :haveRatingProduct="true"/>   
                                </router-link>     
                            </el-col>
                        </el-row>
                        <div class="d-flex justify-contents-center" v-if="productsToShow < products.length || products.length > productsToShow">
                            <a class="btn btn--primary btn--medium is-plain view-more" @click="productsToShow += 6">Tải thêm</a>
                        </div>
                    </div>
                </div>
             </div>
        </div>
    </div>
</template>

<script>

import { mapActions, mapState } from 'vuex'
import ProductItem from '@/components/ProductItem.vue'

export default {
    name: "ListSuggestedProducts",
    components: {
        ProductItem
    },
    data() {
        return {
            loading: false,
           // products: []
           productsToShow: 12,
        }
    },
    computed: {
        ...mapState('products', ['products'])
    },
    methods: {
        ...mapActions('products', ['getProducts']),

        percent_discount(productIndex) {
            return Math.round(100 - (this.products[productIndex].price / this.products[productIndex].list_price) * 100);
        },
        
    },
  
    mounted() { 
        this.getProducts();
    }

}
</script>

<style lang="scss" scoped>
   .carousel-wrapper__item {
       margin-bottom: 12px;
   }
   .view-more {
       width: 224px;
   }
</style>


