<template>
    <li class="lastest-news-lists__item">
        <div class="d-flex">
            <div class="lastest-news-image">
                <a target="_blank" :href="link">
                    <img :src="image_path" alt="">
                </a>
            </div>
            
            <div class="lastest-news-contents">
                <a target="_blank" :href="link">
                    <p> {{ content }}</p>
                </a>
            </div>
        </div>
    </li>
</template>

<script>
export default {
    name: "PostItem",
    props: {
        link:  {
            type: String,
            required: true
        },
        image_path: {
            type: String,

        },
        content: {
            type: String,
            required: true
        }
    }
}
</script>

<style>

</style>


