<template>
  <div class="container-wrapper">
        <div class="tio-section tio-section--banner-advertising">
            <el-row :gutter="12">
                    <el-col :md="8" :lg="8"
                        v-for="item in listBannerAdvantising"
                        :key="item.id">
                        <BannerItem :image_path="item.imagePath" /> 
                    </el-col>
            </el-row>
        </div>
  </div>
</template>

<script>
import BannerItem from '../../../components/BannerItem.vue'
export default {
    name: "BannerAdvertisingTop",
    components: {
        BannerItem
    },
    data() {
        return {
            listBannerAdvantising: [
                {
                    id: 1,
                    imagePath: [
                        'https://f37-zpg.zdn.vn/7966153679173520251/795bcd09ca323e6c6723.jpg'
                    ]
                },
                 {
                    id: 2,
                    imagePath: [
                        'https://f42-zpg.zdn.vn/8354589666245350083/991ed14fd674222a7b65.jpg'
                    ]
                },
                 {
                    id: 3,
                    imagePath: [
                        'https://f38-zpg.zdn.vn/1032265045846047513/33de4c8f4bb4bfeae6a5.jpg'
                    ]
                }
            ]
        }
    }
}
</script>

<style lang="scss" scoped>
    .tio-section {
        &--banner-advertising {
            background-color: transparent;
        } 
    }
    .banner-item {
        height: 164px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
    }
</style>


