<template>
    <div class="container-wrapper">
        <div class="cart" v-if="cartItemCount > 0">
            <el-row :gutter="24">
                <el-col :lg="17">
                    <div class="cart-inner">
                        <div class="product-heading">
                            <el-row :gutter="20">
                                <el-col :lg="11">
                                    <el-checkbox v-model="checkedAll">Tất cả ({{ cartItemCount }} sản phẩm)</el-checkbox>
                                </el-col>
                                <el-col :lg="4">
                                    <div class="product-heading__title">Đơn giá</div>
                                </el-col>
                                <el-col :lg="4">
                                   <div class="product-heading__title">Số lượng</div>
                                </el-col>
                                <el-col :lg="4">
                                   <div class="product-heading__title">Thành tiền</div>
                                </el-col>
                                 <el-col :lg="1">
                                    <i class="el-icon-delete"></i>
                                </el-col>
                            </el-row>
                        </div>
                        <div class="product-content">
                            <div class="product-intended-item" v-for="(product, index) in cart" :key="index">
                                <el-row :gutter="20">
                                    <el-col :lg="11">
                                        <div class="product-intended">
                                            <div class="product-intended__checkbox">
                                                <!-- <el-checkbox :value="product.isChecked" :checked="product.isChecked" @change="updateProductChecked({
                                                    prodId: index,
                                                    checked: $event
                                                })">
                                                </el-checkbox> -->
                                                <el-checkbox  :value="product.isChecked" 
                                                    @change="updateProductChecked({
                                                        prodId: product.id,
                                                        checked: $event
                                                    })">
                                                </el-checkbox>
                                            </div>
                                            <a class="product-intended__image">
                                                <img :src="product.images[0]" :alt="product.name" />
                                            </a>
                                            <div class="product-intended__content">
                                                <a class="product-intended__name" href="#">{{ product.name }}</a>
                                                <div class="product-intended__supplier">
                                                    Nhà cung cấp:&nbsp;
                                                    <a href="#">Tienich Trading</a>
                                                </div>
                                                <div class="product-intended__option">
                                                    <span>Màu: đen</span>
                                                </div>
                                            </div>
                                        </div>
                                    </el-col>
                                    <el-col :lg="4">
                                        <div class="product-intended__price">
                                            <span class="product-intended__real-price">{{ formatValue(product.price) }}₫</span>
                                            <div class="product-intended__discount-prices">
                                                <del>{{ formatValue(product.list_price) }}₫</del>
                                                <span>-{{ percent_discount(product) }}%</span>
                                            </div>
                                        </div>
                                    </el-col>
                                    <el-col :lg="4">
                                        <div class="product-qty product-intended__qty">
                                            <el-input-number v-model="product.count" size="mini" @change="handleChangeQty" :min="1"></el-input-number>
                                        </div>
                                    </el-col>
                                    <el-col :lg="4">
                                        <div class="product-intended__final-prices">{{ formatValue(product.count * product.price) }}₫</div>
                                    </el-col>
                                    <el-col :lg="1">
                                        <div class="product-intended__delete">
                                            <i class="el-icon-delete" @click="removeProductFromCart(product)"></i>
                                        </div>
                                    </el-col>
                                </el-row>
                            </div>
                        </div>
                    </div>
                </el-col>
                <el-col :lg="7">
                    <div class="cart-total-prices">
                        <div class="cart-total-prices__inner">
                            <div class="cart-coupon">
                                <label class="art-coupon__label">Mã giảm giá</label>
                                <div style="margin-top: 12px">
                                    <el-input placeholder="Mã giảm giá" v-model="coupon" size="medium">
                                        <el-button slot="append" @click="handleAddCoupon(coupon)">Áp dụng</el-button>
                                    </el-input>
                                </div>
                            </div>
                            <div class="cart-prices">
                                <div class="cart-prices__item">
                                    <div class="prices__text">Tạm tính</div>
                                    <div class="prices__value">{{ formatValue(cartTotalPrice) }}₫</div>
                                </div>
                                <button class="btn btn--danger w-100">ĐẶT HÀNG</button>
                            </div>
                        </div>
                    </div>
                </el-col>
            </el-row>
        </div>
        <div class="cart-empty" v-else>
            <img src="@/assets/shopping_cart_24px.svg" />
            <p>Không có sản phẩm nào</p>
            <router-link tag="a" to="/" class="btn btn--blue">Tiếp tục mua sắm</router-link>
        </div>
    </div>
</template>

<script>
import mixin from '@/mixins/formatCurrency'
import { mapGetters, mapActions, mapState } from 'vuex'
export default {
    name: 'Cart',
    mixins: [mixin],
    data() {
      return {
        centerDialogVisible: false,
        count: 1,
        coupon: ''
      };    
    },
    computed: {
        checkedAll: {
            get () {
                return this.cart.every(producInCart => producInCart.isChecked)
            },
            set (value) {
                this.updateAllProductChecked(value)
            }
        },

        ...mapState('cart', ['cart']),
        ...mapGetters('cart',['cartItemCount', 'cartTotalPrice']),
    },
    methods: {
        handleClose(done) { 
            this.centerDialogVisible == false;            
        },
        ...mapActions('cart', [
            'removeProductFromCart',
            'updateAllProductChecked',
            'updateProductChecked']),
        
        handleChangeQty(value) {
            console.log(value)
        },

        handleAddCoupon(coupon) {
            console.log(coupon)
        },

        percent_discount(product) {
            return Math.round(100 - (product.price / product.list_price) * 100);
        }
    },
   
}
</script>

<style lang="scss" scoped>
    .cart {
        margin-top: 24px;
    }
    .product-heading {
        padding: 14px 16px;
        background-color: #fff;
        margin-bottom: 12px;

        &__title {
            color: #828282;
            text-align: right;
        }
    } 
    .product-content {
        background-color: #fff;
        margin-bottom: 100px;
    }
    .product-intended-item {
        padding: 16px;
        position: relative;

        &::before {
            content: "";
            height: 1px;
            width: calc(100% - 46px);
            position: absolute;
            bottom: 0;
            right: 0;
            background-color: #EDEDED;
        }

        &:last-child {
            &::before {
                display: none;
            }
        }
        
    }
    .product-intended {
        display: flex;

        &__checkbox {
            .el-checkbox {
                margin-right: 16px;
            }
        }

        &__image {
            width: 88px;
            height: 88px;
        }

        img {
            width: 100%;
        }

        &__content {
            padding-left: 16px;
            position: relative;
            width: calc(100% - 118px);
        }

        &__name {
            color: #333333;
            font-size: 14px;
            line-height: 16px;
            margin-bottom: 8px;
            display: block;

            &:hover {
                color: #1976D2;
            }
        }
        &__supplier {
           margin-bottom: 8px;
        }
        &__option {
            color: #828282;
        }
        &__price {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
        }
        &__real-price {
            font-size: 16px;
            line-height: 19px; 
            font-weight: 700;
            margin-bottom: 8px;
        }
        &__discount-prices {
            del {
                font-weight: 300;
                margin-right: 6px;
            } 
        }
        &__qty {
            text-align: right;
        }
        &__final-prices {
            color: #E53935;
            font-size: 16px;
            font-weight: 700;
            line-height: 18px;
            text-align: right;
            margin-top: 15px;
        }
        &__delete {
            margin-top: 15px;
        }
    }

    .cart-coupon {
        padding: 16px;
        background-color: #fff;
        margin-bottom: 12px;
        &__label {
            font-size: 14px;
            line-height: 16px;
        }
    }
    .cart-prices {
        background-color: #fff;
        padding: 20px 16px 16px 16px;
        &__item {
            display: flex;
            justify-content: space-between;
            margin-bottom: 16px;
        }
        .prices__text {
            color: #828282;
            font-size: 14px;
            line-height: 16px;
        }
        .prices__value {
            font-weight: 500;
            font-size: 18px;
            line-height: 21px;
        }
    }
    .cart-empty {
        display: flex;
        flex-direction: column;
        padding: 210px 0;
        align-items: center;

        p { 
            margin-top: 24px;
            margin-bottom: 24px;
        }
        .btn {
            width: 239px;
            border-radius: 48px;
            text-transform: uppercase;
            padding: 14px 20px;
        }
    }
</style>


