<template>
    <!-- <el-breadcrumb separator-class="el-icon-arrow-right"> -->
        <!-- <el-breadcrumb-item :to="{ path: '/' }">Trang chủ</el-breadcrumb-item>
        <el-breadcrumb-item>Điện gia dụng</el-breadcrumb-item>
        <el-breadcrumb-item>Đồ dùng nhà bếp</el-breadcrumb-item>
        <el-breadcrumb-item>Name product</el-breadcrumb-item> -->
        <!-- <el-breadcrumb-item v-for="breadcrumb in breadcrumbs" :key="breadcrumb.category_id" :to="{ path: breadcrumb.url }">{{ breadcrumb.name }}</el-breadcrumb-item> -->
    <!-- </el-breadcrumb> -->
    <div class="breadcrumb">
        <span class="breadcrumb__item" 
            v-for="breadcrumb in breadcrumbs" 
            :key="breadcrumb.category_id"> 
            <router-link
                class="breadcrumb__inner"
                :to="{ path: breadcrumb.url }"
                >{{ breadcrumb.name }} 
            </router-link>
            <i class="breadcrumb__separator el-icon-arrow-right"></i>
        </span>
    </div>
</template>

<script>
export default {
    name: 'BreadcrumbItem',
    props:['breadcrumbs']
}
</script>

<style lang="scss" scoped>
    .breadcrumb {
        display: flex;
        overflow: hidden;

        &__item {
            display: flex;

            &:last-child {

                .breadcrumb__inner {
                    color: #828282;
                }

                .breadcrumb__separator  {
                    display: none;
                }
            }
        }

        &__inner {
            color: #3282B8;
            white-space: nowrap;
        }

        &__separator {
            color: #A1A1A1;
            margin-left: 10px;
            margin-right: 10px;
        }
    }
</style>


