<template>
  <div class="container-wrapper">
      <div class="tio-section tio-section--featured-posts">
           <div class="tio-section__header">
                <span class="tio-section__title">Những bài viết nổi bật Tuần này</span>
                  <div class="tio-section__link">
                    <span>Xem tất cả bài viết</span>
                    <i class="fas fa-chevron-right"></i>
                </div>
            </div>
            <div class="tio-section__body">
                <div class="section-featured-post">
                    <el-row :gutter="32">
                        <el-col :md="14" :lg="14">
                            <div v-for="item in slicedfirstArray"
                                :key="item.id"
                                class="section-featured-post__full-thumbnail" 
                                :style="{ backgroundImage: 'url(' + item.imagePath + ')' }">
                                <span class="title">{{ item.content }}</span>
                            </div>
                        </el-col>
                        <el-col :md="10" :lg="10"> 
                             <ul class="lastest-news-lists lastest-news-lists--small">
                                <PostItem v-for="item in listLastestNews"
                                    :key="item.id"
                                    :link="item.link"
                                    :image_path="item.imagePath"
                                    :content="item.content"/>
                            </ul>
                        </el-col>
                    </el-row>
                </div>
            </div>
      </div>
  </div>
</template>

<script>
import PostItem from '../../../components/PostItem.vue'
export default {
    name: "FeaturedPost",
    components: {
         PostItem
    },
    data () {
        return {
              listLastestNews: [
                {
                    id: 1,
                    link: 'https://ictnews.vietnamnet.vn/san-pham-so/apple-ra-mat-iphone-12-mini-gia-699-usd-iphone-12-gia-799-usd-iphone-12-pro-gia-999-usd-iphone-12-pro-max-gia-1-099-usd-266783.html#:~:text=iPhone%2012%20nh%E1%BA%ADn%20%C4%91%E1%BA%B7t%20tr%C6%B0%E1%BB%9Bc,s%E1%BA%BD%20%C4%91%C6%B0%E1%BB%A3c%20Apple%20gi%E1%BA%A3m%20gi%C3%A1.',
                    imagePath: 'https://f17-zpg.zdn.vn/3483596001959583306/6ca9f08fb161453f1c70.jpg',
                    content: 'Apple đã chính thức ra mắt iPhone 12 mini giá 699 USD, iPhone 12 giá 799 USD, iPhone 12 Pro giá 999 USD, iPhone 12 Pro Max giá 1.099 USD. Cả 4 mẫu iPhone đều được trang bị kết nối 5G và thiết kế mới lạ.'
                },
                {
                    id: 2,
                    link: 'https://lifestyle.vn/top-10-mau-tv-4k-tot-nhat-dau-nam-2020/',
                    imagePath: 'https://f20-zpg.zdn.vn/4002098219721900837/087eee685c7fa821f16e.jpg',
                    content: 'Đây đều là những mẫu TV đến từ các thương hiệu có tên tuổi như Sony, LG, Samsung… cùng những cái tên đáng chú ý khác trong ngành công nghiệp.'
                },
                {
                    id: 3,
                    link: 'https://lifestyle.vn/samsung-ra-mat-toan-cau-dong-tu-lanh-thoi-trang-bespoke-thiet-ke-module-doi-mau-tuy-thich/',
                    imagePath: 'https://f41-zpg.zdn.vn/1954682754057790048/efaf72a4c9b33ded64a2.jpg',
                    content: 'Samsung ra mắt toàn cầu dòng tủ lạnh thời trang Bespoke: Thiết kế module, đổi màu tùy thích'
                },
                {
                    id: 4,
                    link: 'https://lifestyle.vn/asus-ra-mat-zephyrus-duo-15-core-i9-rtx-2080-super-gpu-2-man-hinh-cam-ung-4k/',
                    imagePath: 'https://f40-zpg.zdn.vn/7758212086271823657/019dbc469650620e3b41.jpg',
                    content: 'Asus ra mắt Zephyrus Duo 15: Core i9, RTX 2080 Super GPU, 2 màn hình, cảm ứng 4K'
                },
                {
                    id: 5,
                    link: 'https://lifestyle.vn/15-ung-dung-goi-video-thu-vi-tren-android-khi-khong-co-facetime/',
                    imagePath: 'https://f42-zpg.zdn.vn/1691682139534391088/a27c4e06f0ec04b25dfd.jpg',
                    content: '15 ỨNG DỤNG GỌI VIDEO THÚ VỊ TRÊN ANDROID KHI KHÔNG CÓ FACETIME'
                }
            ]
        }
    },
    computed: {
        slicedfirstArray() {
            return this.listLastestNews.slice(0, 1)
        }
    }
}
</script>

<style lang="scss" scoped>
    .tio-section {
        &--featured-posts {
            background-color: transparent;
        }
        &__header {
            padding: 0 0 20px 0;
            border-bottom: 0;
        }
    }
    .section-featured-post {
        padding: 16px;
        background-color: #fff;

        &__full-thumbnail {
            height: 355px;
            padding: 24px 20px;
            display: flex;
            align-items: flex-end;
            .title {
                color: #fff;
                font-size: 22px;
                line-height: 32px;
                font-weight: 500;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
            }
        }
    }
    .lastest-news-lists {
        &--small {
            .lastest-news-image { display: none;}
        }
        &__item {
            padding: 12px 0;
            margin: 0;
            &:first-child {
                padding-top: 0;
                border-top: none;
            }

            &:last-child {
                padding-bottom: 0;
            }
        }
    }
   
   
</style>


