<template>
    <div class="countdown">
        <span class="countdown-item">{{ hours | two_digits }}</span>
        <span class="countdown-item">{{ minutes | two_digits }}</span>
        <span class="countdown-item">{{ seconds | two_digits }}</span>
    </div>
</template>

<script>
export default {
    name: 'CountDown',
    filters: {
        two_digits: (value) => {
            if (value < 0) {
                return '';
            }
            if (value.toString().length <= 1) {
                return `0${value}`;
            }
            return value;
        }
    },
    mounted() {
        window.setInterval(() => {
            this.now = Math.trunc((new Date()).getTime() / 1000);
        },1000);
    },
    props: {
        date: {
            type: String
        }
    },
    data() {
        return {
            now: Math.trunc((new Date()).getTime() / 1000)
        }
    },
    computed: {
        dateInMilliseconds() {
            return Math.trunc(Date.parse(this.date) / 1000)
        },
        seconds() {
            return (this.dateInMilliseconds - this.now) % 60;
        },
        minutes() {
            return Math.trunc((this.dateInMilliseconds - this.now) / 60) % 60;
        },
        hours() {
            return Math.trunc((this.dateInMilliseconds - this.now) / 60 / 60);
        },
        
    }
}
</script>

<style lang="scss" scoped>
@import "../scss/abstracts/_variables.scss";
    .countdown-item {
        background-color: $color-black;
        border-radius: 4px;
        color: $color-white;
        padding: 2px 6px;
        font-size: $fs-16;
        line-height: 19px;
        font-weight: 500;
    }
</style>


