import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home/index'
import ProductDetail from '@/views/ProductDetail/index'
import Cart from '@/views/Cart/index'
import Page404 from '../views/ErrorPage/404';


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      name: 'Page404',
      component: Page404
    },
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/product-details/:Pid',
      name: 'ProductDetail',
      component: ProductDetail,
      props: true
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart,
    }
  ]
})



