const state = {
    cart: JSON.parse(window.localStorage.getItem('cart-products')) || [],
}

const getters = {
    cartItemCount (state) {
        return state.cart.length
    },

    cartTotalPrice (state) {
        let total = 0;
        state.cart.forEach(item => {
            if (item.isChecked) {
                total += item.price * item.count;
            }
        });

        return total;
    }
}

const actions = {
    addProductToCart({ commit, dispatch }, { product, count } ) {
        commit('ADD_TO_CART', { product, count })
        dispatch('notification/addNotification', {
            message: 'Thêm vào giỏ thành công',
            type: "info",
            multiple: false
        }, { root: true })
    }, 

    removeProductFromCart({commit}, product) {
        commit('REMOVE_PRODUCT_FROM_CART', product)
    },

    updateAllProductChecked ({ commit }, checked) {
        commit('UPDATE_ALL_PRODUCT_CHECKED', checked)
    },

    updateProductChecked({commit},{ prodId, checked }) {
        commit('UPDATE_PRODUCT_CHECKED', { prodId, checked } )
    }
}

const mutations = {
    ADD_TO_CART (state, { product, count }) {
        let productInCart = state.cart.find(item => item.id === product.id)
        if (productInCart) {
            productInCart.count+=count
            productInCart.isChecked = true
            return;
        } else {
            state.cart.push({
                ...product,
                count,
                isChecked: false
            })
        }
       
    },

    REMOVE_PRODUCT_FROM_CART(state, product) {
        const index = state.cart.findIndex(item => item.id === product.id)
        index !==-1 && state.cart.splice(index, 1)
        // state.cart = state.cart.filter(item => {
        //     return item.product_id !== product.product_id 
        // })
    },

    UPDATE_ALL_PRODUCT_CHECKED(state, checked) {
        state.cart.forEach(productInCart => {
            productInCart.isChecked = checked
        })
    },

    UPDATE_PRODUCT_CHECKED(state, { prodId, checked } ) {
        const productInCart = state.cart.find(item => item.id === prodId)
        productInCart && (productInCart.isChecked = checked)
    },
}


export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}


