<template>
    <div class="container-wrapper">
        <div class="tio-section">
           <!-- <div  v-for="product in products" :key="product.id"> -->
               <!-- {{ item }} -->
                <div class="product-briefing">
                    <div class="product-briefing__carousel">
                        <ProductThumbDetail :photosfulls="product.images" />
                    </div>
                    <div class="product-briefing__space-block"></div>
                    <div class="product-briefing__content">
                        <div class="header">
                                <h1 class="title">{{ product.name }}</h1>
                                <div class="below-title d-flex">
                                    <el-rate v-model="product.rating_average"  disabled-void-color="#E0E0E0" void-color="#FB8C00" disabled></el-rate> 
                                    <span style="margin-left: 8px">{{ product.review_count }} đánh giá</span>
                                </div>
                                <div class="brand d-flex">
                                    <div class="brand-and-author">
                                        Thương hiệu:&nbsp;<a href="#">{{ product.brand }}</a>
                                    </div>
                                    <div class="supplier">
                                        Nhà cung cấp:&nbsp;<a href="#">Tienich Trading</a>
                                    </div>
                                </div>
                        </div>
                        <div class="body">
                            <div class="sumary">
                                <div class="left">
                                        <div class="describe-product">
                                            <ul>
                                                <li>{{ product.short_description }}</li>
                                            </ul>
                                        </div>
                                        <div class="product-option-wrapper" >
                                                <div class="variant-select-wrapper" v-for="variant in product.configurable_options" :key="variant.code">
                                                    <p class="option-name">{{ variant.name }}: 
                                                        <span><b> {{ selected }} </b></span>
                                                    </p>
                                                    <div class="option-list-wrapper">
                                                        <div class="option-button"
                                                            v-for="item in variant.values"
                                                            :class="{ isActive:item.label == selected }"
                                                            :key="item.label"
                                                            @click="selected = item.label"
                                                            >
                                                            {{ item.label }}
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="quantity-add-card">
                                                    <p>Số lượng</p>
                                                    <div class="product-qty">
                                                        <el-input-number v-model="count" size="mini" @change="handleChange" :min="1"></el-input-number>
                                                    </div>
                                                </div>
                                        </div>
                                        <div class="add-to-cart">
                                            <div class="product-price">
                                                <div>
                                                    <span>Giá:</span>
                                                    <span class="product-price__price">{{ formatValue(product.price) }}₫</span>
                                                </div>
                                                <div>
                                                    <span style="visibility: hidden;">Giá:</span>
                                                    <span class="product-price__discount">{{ formatValue(product.list_price) }}₫</span>
                                                    <span>-{{ percent_discount(product) }}%</span>
                                                </div>
                                            </div>
                                            <div class="group-button">
                                                <button class="btn btn--danger btn-buy-now">Mua ngay</button>
                                                <button class="btn btn--warning btn-add-to-cart" @click="addToCart(product)">Thêm vào giỏ hàng</button>
                                            </div>
                                        </div>
                                </div>
                                <div class="right">
                                    <div class="benefit">
                                        <div class="benefit__item">
                                                <img  src="@/assets/benefit_1.svg" alt=""/>
                                                <span>Cam kết <b>100%</b> hàng chính hãng</span>
                                        </div>
                                            <div class="benefit__item">
                                                <img  src="@/assets/benefit_2.svg" alt=""/>
                                                <span><b>20 ngày</b> đổi trả hàng</span>
                                        </div>
                                        <div class="benefit__item">
                                                <img  src="@/assets/benefit_3.svg" alt=""/>
                                                <span>Bảo hành điện tử <b>12 tháng</b>
                                                    <br><a href="#">Xem chi tiết</a>
                                                </span>         
                                        </div>
                                    </div>
                                    <div class="benefit mb-0">
                                        <div class="benefit__title">Khuyến mãi & ưu đãi</div>
                                        <p>
                                                - Hoàn tiền 12% (tối đa 100k) khi thanh toán bằng <b>Tienich Pay</b> - <a href="#">Chi tiết</a>
                                        </p>
                                        <p>
                                                - Giảm 300k tối đa 900k khi thanh toán bằng thẻ <b>SHB</b> đơn hàng từ 1 triệu - <a href="#">Chi tiết</a>
                                        </p>
                                        <p>- Nhập mã <b class="discount-code">TIENICH68</b> giảm ngay 300k đơn hàng từ 1 triệu, HSD 31/4/2020.</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div> 
            <!-- </div> -->
        </div>
    </div>
</template>

<script>
//import api from '@/api/data.json'
import ProductThumbDetail from './ProductThumbDetail.vue'
import mixin from '@/mixins/formatCurrency'
import { mapState, mapActions, mapMutations } from 'vuex'
export default {
    name: "DetailProduct",
    mixins: [mixin],
    components: { ProductThumbDetail },
    props: ['product'],
    data() {
        return {
            count: 1,
            selected: undefined,
        }
    },
    mounted () {
  
    },
    // computed: {
    //    ...mapState('products', ['products'])
    // },
    // created() {
    //     this.loading= true
    //     this.$store.dispatch('fetchProducts')
    //         .then(() => this.loading = false)
    // },
    methods: {
        //...mapActions('products', ['getProducts']),
        //...mapMutations('cart',['addProductToCart']),
        ...mapActions('cart',['addProductToCart']),

        addToCart(product) {
            this.addProductToCart({
                product: product,
                count: this.count
            })

        },
    
        handleChange(value) {
            console.log(value)
        },

        percent_discount(product) {
            return Math.round(100 - (product.price / product.list_price) * 100);
        },

       
    },
    // created() {
    //     this.getProducts()
    // }
}
</script>

<style lang="scss" scoped>
    
</style>


