<template>
    <el-dialog  :visible.sync="editVisible" 
        :before-close="handleClose" 
        :close-on-click-modal="false"
        custom-class="el-dialog-default"
        center>
        <el-tabs v-model="activeName" @tab-click="handleClick" class="custom-tab-default">
            <el-tab-pane label="Đăng nhập" name="login">
                <div class="form-modal-wrapper">
                    <el-form :label-position="labelPosition" :rules="rules" label-width="100px" :model="formLogin" ref="formLogin">
                        <el-form-item prop="number_phone" label="Số điện thoại">
                            <el-input v-model="formLogin.number_phone" placeholder="Nhập số điện thoại"></el-input>
                        </el-form-item>
                        <el-form-item label="Mật khẩu" prop="password">
                            <el-input v-model="formLogin.password" placeholder="Nhập mật khẩu" show-password></el-input>
                        </el-form-item>
                        <el-form-item>
                            <button class="btn btn--green btn-login"  @click="submitForm('formLogin')">Đăng nhập</button>
                        </el-form-item>
                    </el-form>
                    <div class="forgot-pw">
                        <span class="forgot-pw__text">Quên mật khẩu?</span>
                    </div>
                </div>
            </el-tab-pane>
            <el-tab-pane label="Đăng kí" name="register">
                <div class="form-modal-wrapper">
                    <el-form :label-position="labelPosition" :rules="rules" label-width="100px" :model="formRegister" ref="formRegister">
                        <el-form-item prop="full_name" label="Tên người dùng">
                            <el-input v-model="formRegister.full_name" placeholder="Nhập họ và tên"></el-input>
                        </el-form-item>
                        <el-form-item prop="number_phone" label="Số điện thoại">
                            <el-input v-model="formRegister.number_phone" placeholder="Nhập số điện thoại"></el-input>
                        </el-form-item>
                        <el-form-item label="Mật khẩu" prop="password">
                            <el-input v-model="formRegister.password" placeholder="Nhập mật khẩu" show-password></el-input>
                        </el-form-item>
                        <el-form-item>
                            <button class="btn btn--green btn-register"  @click="submitForm('formRegister')">Tạo tài khoản</button>
                        </el-form-item>
                    </el-form>
                </div>
            </el-tab-pane>
        </el-tabs>
    </el-dialog>
</template>

<script>
export default {
    name: 'ModalLoginRegister',
    props: {
        editVisible: {
            type: Boolean,
            default: false
        },
    },
    data() {
        var checkPhoneNumber = (rule, value, callback) => {
           const phoneReg = /((09|03|07|08|05)+([0-9]{8})\b)/g;
            if(!value) {
                return callback(new Error('Vui lòng nhập số điện thoại'));
            }
            setTimeout(() => {
                if(!Number.isInteger(+value)) {
                    callback(new Error('Vui lòng nhập kí tự là chữ số'));
                } else {
                    if(phoneReg.test(value)) {
                        callback();
                    } else {
                        callback(new Error('Vui lòng nhập đúng định dạng số điện thoại'));
                    }
                }
            }, 1000)   
        }; 
        var checkPassword = (rule, value, callback) => {
            const pwReg = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z]{8,}$/;
            if(!value || value.trim().length < 8) {
                return callback(new Error('Mật khẩu phải có ít nhất 8 kí tự'));
            } 
            setTimeout(() => {  
                if(pwReg.test(value)) {
                    callback();
                } else {
                    callback(new Error('Vui lòng nhập mật khẩu đúng định dạng'))
                }
            })
        };
        var checkfullName = (rule, value, callback) =>  {
            if(!value) {
                return callback(new Error('Vui lòng nhập họ và tên'))
            }
        }
        return {
            activeName: 'login',
            labelPosition: 'top',
            formLogin: {
                number_phone: '',
                password: ''
            },
            formRegister: {
                number_phone: '',
                password: ''
            },
            rules: {
                number_phone: [
                    { validator: checkPhoneNumber, trigger: 'blur' }
                ],
                password: [
                    { validator: checkPassword, trigger: 'blur' }
                ],
                full_name: [
                    { validator: checkfullName, trigger: 'blur' }
                ],
            }
            
        };
    },
    methods: {
        handleClose() {
            this.$emit("update:editVisible",!this.editVisible);
        },
        handleClick(tab, event) {
            console.log(tab, event);
            this.$emit("queryCoures", this.activeName);
        },
      
        submitForm(formName) { 
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('submit!');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        }
    }
}
</script>

<style lang="scss" scoped>
    
</style>


