<template>
    <div class="carousel-wrapper__item">
        <a class="product-item" href="#">
            <div class="product-item__thumnail">
                <img :src="image[0]" alt="" />
            </div>
            <div class="product-item__info">
                <div style="margin-top: 12px"></div>
                <div class="name">
                   {{ name }}
                </div>
                <div class="price-discount">
                    <div class="price-discount__price">{{ formatValue(price) }}₫</div>
                    <div v-if="havePriceDiscount" class="price-discount__discount">{{ formatValue(list_price) }}₫</div>
                    <div class="price-discount__discount" v-else>-{{ percent_discount }}%</div>
                </div>
                <div class="process-line" v-if="haveProcess">
                    <el-progress :stroke-width="4" :show-text="false" :percentage="percentage" :color="`#E0AE09`"></el-progress>
                </div>
                <div class="product-quantity-sold">
                    <div class="d-flex" v-if="haveRatingProduct">
                        <el-rate v-model="rating_average" disabled-void-color="#E0E0E0" void-color="#FB8C00" disabled></el-rate> 
                        <span style="margin-left: 4px">({{ quantity_sold }})</span>
                    </div>
                    <div v-else>Đã bán {{ quantity_sold }}</div>
                </div>
            </div>
        </a>
    </div>
</template>

<script>
import mixin from '@/mixins/formatCurrency'
export default {
    name: 'ProductItem',
    props: {
        haveRatingProduct: {
            type: Boolean,
            default: false
        },
        name: {
            type: String,
            required: true,
        },
        price: {
            type: Number,
            default: 0,
        },
        list_price: {
            type: Number,
            default: 0,
        },
        percent_discount: {
            type: Number,
            default: 0
        },
        percentage: {
            type: Number,
            default: 0,
        },
        quantity_sold: {
            type: Number,
            default: 0,
        },
        image: {
            type: Array,
        },
        haveProcess: {
            type: Boolean,
            default: false
        },
        havePriceDiscount: {
            type: Boolean,
            default: true
        },
        rating_average: {
            type: Number,
            default: null
        }
    },
    mixins: [mixin],
    methods: {
        // formatPrice(value) {
        //     return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
        // },
    }
}
</script>

<style lang="scss" scoped>
    .el-rate {
        height: auto;
    }
    .el-rate >>> .el-rate__icon {
        font-size: 16px;
        margin-right: -1px;
    }
</style>


