<template>
  <div class="vue-gallery">
        <div class="activePhoto" 
            :style="'background-image: url('+photosfulls[activePhoto]+')'"
             @click="modalSliderProductDetail = true">
            <!-- <button type="button" aria-label="Previous Photo" class="previous" @click="previousPhoto()">
                <i class="fas fa-chevron-circle-left"></i>
            </button>
            <button type="button" aria-label="Next Photo" class="next" @click="nextPhoto()">
                <i class="fas fa-chevron-circle-right"></i>
            </button> -->
        </div>
        <div class="thumbnails" v-if="photosfulls.length > 6">
            <div
                v-for="(photo, index) in photosfulls.slice(0,5)"
                :src="photo"
                :key="index"
                @click="activePhoto = index"
                :class="{'active': activePhoto == index}" :style="'background-image: url('+photo+')'">
            </div>
            <div class="thumbnails__open-gallery" 
                :style="'background-image: url('+photosfulls[5]+')'"
                @click="modalSliderProductDetail = true"
                >
                <span>Xem thêm {{ photosfulls.length - 5 }} ảnh</span>
            </div>
        </div>
         <div class="thumbnails" v-else>
            <div
                v-for="(photo, index) in photosfulls"
                :src="photo"
                :key="index"
                @click="activePhoto = index"
                :class="{'active': activePhoto == index}" :style="'background-image: url('+photo+')'">
            </div>
         </div>
        <div class="free-shipping-text">
            <img src="@/assets/local_shipping_24px.svg" alt=""/>
           <span>Miễn phí vận chuyển cho đơn hàng trên 150.000đ</span> 
        </div>
         <el-dialog
            :visible.sync="modalSliderProductDetail"
            fullscreen
            custom-class="view-fullscreen"
            >
            <div class="image-viewer-wrapper">
                <div class="image-viewer">
                    <img :src="photosfulls[activePhotoslider]"  alt="" />
                    <span type="button" class="previous" @click="previousPhoto()"
                        :class="{'disabled' : activePhotoslider == 0}"
                         v-if="photosfulls.length > 1">
                        <img src="@/assets/previous.svg" alt=""/>
                    </span>
                    <span class="next" @click="nextPhoto()"
                        :class="{'disabled' : activePhotoslider == photosfulls.length-1}"
                        v-if="photosfulls.length > 1">
                        <img src="@/assets/next.svg" alt=""/>
                    </span>
                </div>
                <div class="thumbnails">
                    <div
                        v-for="(photo, index) in photosfulls"
                        :src="photo"
                        :key="index"
                        @click="activePhotoslider = index"
                        :class="{'active':  activePhotoslider == index}" :style="'background-image: url('+photo+')'">
                    </div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: "ProductThumbDetail",
    props: {
        photosfulls: {
            type: Array
        }
    },
    data () {
        return {
            activePhoto: null,
            activePhotoslider: null,
            modalSliderProductDetail: false
        }
    },
    mounted () {
        this.activePhoto = 0
        this.activePhotoslider = 0
    },
    methods: {
        nextPhoto () {
            this.activePhotoslider = ( this.activePhotoslider+1 < this.photosfulls.length ? this.activePhotoslider+1 : this.photosfulls.length-1 )
        },
        previousPhoto () {
            this.activePhotoslider = ( this.activePhotoslider-1 >= 0 ? this.activePhotoslider-1 : 0 )
        }
    }
}
</script>

<style lang="scss" scoped>
    .free-shipping-text {
        padding-left: 10px;
        display: flex;
        align-items: center;
        span {
            color: #828282;
            margin-left: 8px;
        }
    }
   
  
</style>


