<template>
    <div class="container-wrapper">
        <div class="breadcrum-section">
            <BreadcrumbItem :breadcrumbs="breadcrumbs" />
        </div>
    </div>
</template>

<script>
import BreadcrumbItem from '@/components/BreadcrumbItem.vue'
export default {
    components: { BreadcrumbItem },
    props: ["breadcrumbs"]
}
</script>

<style lang="scss" scoped>
    .breadcrum-section {
        padding: 16px 0;
    }
</style>


