<template>
    <div class="container-wrapper">
        <div class="tio-section">
            <div class="section-desc">
                <el-row :gutter="52">
                    <el-col :md="15">
                        <div class="description">
                            <div class="description__content">
                                <div class="toggle-content"
                                :class="{ readMore: isReadMore }" v-html="product.description">
                                </div>
                                <div v-if="isReadMore == false" class="gradient"></div>
                            </div>
                            <button class="btn btn--primary btn--medium is-plain btn-more" @click="isReadMore = !isReadMore">
                                <span v-if="isReadMore">Thu gọn nội dung</span>
                                <span v-else>Xem thêm nội dung</span>
                            </button>
                        </div>
                    </el-col>
                    <el-col :md="9">
                        <div class="section-desc__right-title">
                            <span class="tio-section__title">thông tin chi tiết</span>
                        </div>
                        <div class="tio-section__body">
                            <div class="content-table" v-for="specification in product.specifications" :key="specification.content">  
                                <table>
                                    <tbody>
                                        <tr v-for="item in specification.attributes" :key="item.code">
                                            <td>{{ item.name }}</td>
                                            <td>{{ item.value }}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </el-col>
                </el-row>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'DescriptionProduct',
    props: ['product'],
    data() {
        return {
            isReadMore: false,
        }
    }
}
</script>

<style lang="scss">
    .section-desc {
        background-color: #ffffff;
        padding: 0 24px;
        &__right-title {
            padding: 24px 0;
        }
    }
    .description {
        padding: 24px 0;
       
        &__content {
            position: relative;

            p {
                font-size: 14px;
                line-height: 22px;
                margin-top: 5px;
                margin-bottom: 12px;
            }
            .toggle-content {
                max-height: 800px;
                overflow: hidden;

                &.readMore {
                    max-height: 100%;
                    overflow: auto;
                }
            }

            .gradient {
                position: absolute;
                background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 100%);
                height: 92px;
                bottom: 0;
                left: 0;
                width: 100%;
            }
        }

        .btn-more {
            margin: 0 auto;
            display: block;
        }

    }
    .content-table {
        margin-bottom: 24px;
        table {
            max-width: 100%;
            border-collapse: collapse;
            border-spacing: 0px;
            width: 100%;
            tr {
                &:nth-child(2n+1) {
                    background-color: #F6F6F6;
                }
                td {
                    padding: 12px;
                    &:first-child {
                        width: 135px;
                    }
                }
            }
        }
    }
</style>


