<template>
    <div class="notification-message">
        <img src="@/assets/check_circle_24px.svg" />
        <p class="text-white">{{ notification.message }}</p>
    </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
    name: 'NotificationMessage',
    props: ['notification'],
    // computed: {
    //     ...mapState('notification', ['notification']),
    // }, 
    data() {
        return {
            timeout: null,
        }
    },
    created() {
        this.timeout = setTimeout(() => {
            this.removeNotification(this.notification);
        }, 3000);
    },

    beforeDestroy() {
        clearTimeout(this.timeout);
    },

    methods: {
        ...mapActions('notification', ['removeNotification'])
    }
}
</script>

<style lang="scss">
    .notification-message {
        margin-top: 15px;
        display: flex;
        flex-direction: column;
        background: rgba(0, 0, 0, 0.75);
        border-radius: 16px;
        padding: 36px 24px;
        align-items: center;
        img {
            width: 48px;
            height: 38px;
        }

        p {
            margin-top: 24px;
        }
    }
</style>


