<template>
    <div class="product-detail-page"> 
        <div v-for="product in products" :key="product.id"  v-if="product.id == proId">
            <div>
                <Breadcrumb :breadcrumbs="product.breadcrumbs"/>
                <DetailProduct :product="product"/>
            </div>
            <ProductsPurchasedTogether />
            <DescriptionProduct :product="product"/>
        </div>
    </div>
</template>

<script>
import Breadcrumb from './components/Breadcrumb.vue'
import DetailProduct from './components/DetailProduct.vue'
import ProductsPurchasedTogether from './components/ProductsPurchasedTogether.vue'
import DescriptionProduct from './components/DescriptionProduct.vue'
import { mapState, mapActions } from 'vuex'

export default {
    name: "ProductDetail",
    components: { 
        Breadcrumb,
        DetailProduct,
        ProductsPurchasedTogether,
        DescriptionProduct
    },
    data() {
        return {
            //products: api,
            proId:this.$route.params.Pid,
        }
    },
    computed: {
        ...mapState('products', ['products']),
    },

    methods: {
        ...mapActions('products', ['getProducts']),
    },
    created() {
        this.getProducts()
    }
}
</script>

<style>

</style>


