
<template>
  <div class="container-wrapper">
        <div class="tio-section tio-section--banner-advertising">
            <el-row :gutter="12">
                    <el-col :md="12" :lg="12"
                        v-for="item in listBannerAdvantising"
                        :key="item.id">
                        <BannerItem :image_path="item.imagePath" /> 
                    </el-col>
            </el-row>
        </div>
  </div>
</template>

<script>
import BannerItem from '../../../components/BannerItem.vue'
export default {
    name: "BannerAdvertisingBottom",
    components: {
        BannerItem
    },
    data() {
        return {
            listBannerAdvantising: [
                {
                    id: 1,
                    imagePath: [
                        'https://f41-zpg.zdn.vn/3081812441409702448/b5e080bd2b51df0f8640.jpg'
                    ]
                },
                 {
                    id: 2,
                    imagePath: [
                        'https://f18-zpg.zdn.vn/1085287450659613325/3b313e1695fa61a438eb.jpg'
                    ]
                }
            ]
        }
    }
}
</script>

<style lang="scss" scoped>
    .tio-section {
        &--banner-advertising {
            background-color: transparent;
        } 
    }
    .banner-item {
        height: 125px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
    }
</style>


